import {
  abi,
  tokenAbi
} from '@/metaMask/abi.js'
import config from '@/config'
import BigNumber from 'bignumber.js'
// import Decimal from 'decimal.js'
const Web3 = require('web3')

let web3
if (
  typeof window.ethereum !== 'undefined' ||
  typeof window.web3 !== 'undefined'
) {
  // Web3 browser user detected. You can now use the provider.
  const provider = window.ethereum || window.web3.currentProvider
  web3 = new Web3(provider)
}
try {
  window.web33 = web3.utils
} catch (e) {
  alert(' open MetaMask')
}
const MAX1E18 = '1000000000000000000'
const Bint = BigNumber(MAX1E18)
class HandelWeb3 {
  alldata = [];
  rangData = [];
  mydata = [];
  _instance = null;
  decimals = BigNumber(18);
  userAddress = window.web3.currentProvider.selectedAddress;
  abi = abi;
  contractAddr = config.contractAddr // '0xb01e09ba5eb22399a7f305102073e1f5e9d4c5f3';
  tokenContractAddr = config.tokenContractAddr // '0x1874924ac1429c897b7db77908ccd71eb6098b12';
  tokenAbi = tokenAbi;
  contract = new web3.eth.Contract(this.abi, this.contractAddr);
  contractDeployBlockHeight = 4107412;
  currentBlock = 0;
  inited = false;
  tokenContract = new web3.eth.Contract(this.tokenAbi, this.tokenContractAddr);
  delDate=''
  // constructor () {
  // }

  async init () {
    if (this.currentBlock !== 0) {
      return
    }
    await web3.eth.getBlockNumber()
      .then((num) => {
        this.currentBlock = num
      })
    if (!this.inited) {
      this.inited = true
      this.initDice(this.currentBlock - 1)
    }
  }

  initDice (toBlockHeight, step = 9999) {
    this.contract.getPastEvents('DiceResult', {
      fromBlock: toBlockHeight - step,
      toBlock: toBlockHeight
    }, (error, events) => {
      if (error) {
        return
      }
      for (let i = events.length - 1; i >= 0; i--) {
        if (this.mydata.length >= 20 && this.alldata.length >= 20) return
        if (this.alldata.length < 20) {
          this.alldata.push(HandelWeb3.event2obj(events[i]))
        }
        if (this.mydata.length < 20 && events[i].returnValues.who === web3.utils.toChecksumAddress(window.web3.currentProvider.selectedAddress)) {
          this.mydata.push(HandelWeb3.event2obj(events[i]))
        }
      }
      if (toBlockHeight - step > this.contractDeployBlockHeight) {
        this.initDice(toBlockHeight - step)
      }
    })
  }
  /**
   *
   * @param {投注的金额} _value :number
   * @param {*} _extraData
   */
  approveAndCall (_value, _extraData, callback) {
    let value = web3.utils.toWei(String(_value), 'ether')
    let extraDataStr
    let randomString = web3.utils
      .randomHex(31)
      .substring(2)
      .padStart(31 * 2, '0')
    if (_extraData <= 0xf) {
      extraDataStr = '0x0' + _extraData.toString(16)
    } else {
      extraDataStr = '0x' + _extraData.toString(16)
    }
    let extraData = web3.utils.hexToBytes(extraDataStr + randomString)
    // this.extraData = extraData
    try {
      this.tokenContract.methods
        .approveAndCall(this.contractAddr, web3.utils.toHex(value), extraData)
        .send({
          from: window.web3.currentProvider.selectedAddress,
          to: this.tokenContractAddr
        },
        (error, result) => {
          if (error) {
            callback(error, null)
            throw error
          }
        }
        )
        .catch(error => {
          alert(error)
        })
    } catch (error) {
      alert(error)
    }
  }
  async AllBets (callback) {
    console.debug('allbets.............')
    await this.init()
    this.contract.events.DiceResult({
      fromBlock: this.currentBlock,
      toBlock: 'latest'
    },
    (error, event) => {
      console.debug(error, event)
      if (this.delDate === event.transactionHash) {
        this.delDate = event.transactionHash
        return
      }
      this.delDate = event.transactionHash
      if (error) return
      let objt = HandelWeb3.event2obj(event)

      this.alldata.push(objt)
      if (this.alldata.length >= 20) {
        this.alldata.shift()
      }
      if (objt.name === web3.utils.toChecksumAddress(window.web3.currentProvider.selectedAddress)) {
        callback(error, event)
        this.mydata.push(objt)
        if (this.alldata.length >= 20) {
          this.alldata.shift()
        }
      }
    }
    )
    // return this.alldata
  }

  static event2obj (event) {
    // console.debug(event)
    let objt = {}
    objt.data = event.blockNumber

    objt.name = event.returnValues.who
    objt.transactionHash = event.transactionHash
    // objt.Betting = event.returnValues.result
    // objt.Betting = web3.utils.fromWei(event.returnValues.value, 'ether')
    objt.Betting = BigNumber(event.returnValues.value).div(Bint)

    // objt.Odds = event.
    let Number16 = parseInt(event.returnValues.chosen)
    // console.debug(ArrayNumber)
    let quiz = []
    for (let i = 1; i <= 6; i++) {
      if (Number16 % 2 === 1) {
        quiz.push(i)
      }
      Number16 = Number16 >> 1
    }
    objt.Quiz = event.returnValues.result + '/' + quiz.join(',')
    // objt.Profit = web3.utils.fromWei(event.returnValues.winValue, 'ether') // 收益
    objt.Profit = BigNumber(event.returnValues.winValue).div(Bint) // 收益
    objt.Odds = ((6 / quiz.length) * 0.98).toFixed(2) // 赔率
    return objt
  }

  rangkList (callback) {
    web3.eth.getBlockNumber().then(res => {
      let TiemBlocknumber =
        ((new Date().getTime() / 1000 -
            new Date(new Date().setHours(0, 0, 0, 0)) / 1000) /
          15) |
        0
      console.debug(res, TiemBlocknumber)
      this.contract.events.DiceResult({
        fromBlock: res - TiemBlocknumber
      },
      (error, event) => {
        if (error) { return }
        if (event == null) return
        console.debug(event)
        if (this.rangData.length >= 10) {
          this.rangData.shift()
          return
        }
        let objt = {}

        objt.name = String(event.returnValues.who)

        objt.Betting = BigNumber(event.returnValues.value).div(Bint)

        objt.Profit = BigNumber(event.returnValues.winValue).div(Bint)// 收益
        console.log(objt)
        if (this.rangData[ objt.name ]) { // todo 为什么关联数组不能实现
          // alert(1)
          this.rangData[ objt.name ].Profit = (parseFloat(objt.Profit) + parseFloat(this.rangData[ objt.name ].Profit)).toFixed(2)
          this.rangData[ objt.name ].Betting = (parseFloat(objt.Betting) + parseFloat(this.rangData[ objt.name ].Betting)).toFixed(2)
        } else {
          this.rangData[ objt.name ] = objt
        }

        callback(this.rangData)
      }
      )
    })
  }
  takeWithdraw () {
    console.debug(window.web3.currentProvider.selectedAddress)
    this.contract.methods
      .withdraw()
      .send({
        from: window.web3.currentProvider.selectedAddress,
        to: this.contractAddr
      })
      .then(receipt => {
        console.debug(receipt)
      })
  }
  userBalance () {
    return new Promise((resolve, reject) => {
      this.contract.methods
        .balance(window.web3.currentProvider.selectedAddress)
        .call({
          from: window.web3.currentProvider.selectedAddress,
          to: this.contractAddr
        },
        (error, res) => {
          if (error) { reject(error) }

          if (!res || res === 0) {
            // res = '0'
            return
          }
          // console.log(res.div('100000000000000000'))
          console.debug(res)
          let a = BigNumber(res).div(MAX1E18)
          console.debug('用户赢的钱' + a)
          if (error) return error
          resolve(a)
        }
        )
    })
  }
  balanceOf () {
    return new Promise((resolve, reject) => {
      this.tokenContract.methods.balanceOf(window.web3.currentProvider.selectedAddress).call({
        from: window.web3.currentProvider.selectedAddress,
        to: this.contractAddr
      }, (error, res) => {
        if (error) reject(error)
        // let a = parseFloat(web3.utils.fromWei(res, 'ether'), 2)
        if (!res) {
          res = '0'
        }
        let a = parseFloat(BigNumber(res).div(Bint)).toFixed(2)
        console.debug('用户的小狐狸钱' + a)
        resolve(a)
      })
    })
  }
  dice (_value, _extraData, callback) {
    let value = web3.utils.toWei(String(_value), 'ether')
    let extraDataStr
    let randomString = web3.utils
      .randomHex(31)
      .substring(2)
      .padStart(31 * 2, '0')
    if (_extraData <= 0xf) {
      extraDataStr = '0x0' + _extraData.toString(16)
    } else {
      extraDataStr = '0x' + _extraData.toString(16)
    }
    let extraData = web3.utils.hexToBytes(extraDataStr + randomString)
    this.contract.methods.dice(value, extraData).send({
      from: window.web3.currentProvider.selectedAddress,
      to: this.contractAddr
    }, (error, event) => {
      if (error) {
        callback(error, null)
        throw error
      }
      // this.contract.once(
      //   'DiceResult', {
      //     filter: {
      //       id: extraData
      //     }
      //   },
      //   (error, event) => {
      //     callback(error, event)
      //   }
      // )
    })
  }
  testSign () {

  }
  static getInstance () {
    if (!this._instance) {
      this._instance = new HandelWeb3()
    }
    return this._instance
  }
}
console.debug('web3 加载')
export default HandelWeb3.getInstance()
